          { key: '', name: 'SD4標準家庭房', number: '6219', floor: '2F' },
          { key: '', name: 'SD4標準家庭房', number: '6222', floor: '2F' },
          { key: '', name: 'SD4標準家庭房', number: '6223', floor: '2F' },
          { key: '', name: 'SD4標準家庭房', number: '6225', floor: '2F' },
        ],
        double: [
          { key: '', name: 'ST2標準雙人房', number: '7303', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7308', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7309', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7310', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7311', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7312', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7313', floor: '2F' },
          { key: '', name: 'ST2標準雙人房', number: '7315', floor: '2F' },
        ],
        fancy: [
          { key: '', name: 'AT2雅緻雙人套房', number: '9601', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9602', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9603', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9605', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9606', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9607', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9608', floor: '2F' },
          { key: '', name: 'AT2雅緻雙人套房', number: '9609', floor: '2F' },
        ],
      },
      RoomsTypeOptionList: [
        { code: 'a', name: '四人房二大床' },
        { code: 'b', name: '四人房一大+二小床' },
        { code: 'c', name: '四人房四小床' },
        { code: 'd', name: '三人房三中床' },
        { code: 'e', name: '二人房一大床' },
        { code: 'f', name: '二人房二中床' },
      ],
    }
  },
  components: {
    VueMultiselect,
  },
  methods: {
    // 頁數:切換
    PageChange(Page) {
      if (Page > 0) {
        this.PageInfo.PageIndex = Page
        this.PageInfo.PageJump = Page
        this.ListData()
      }
    },
    // 頁數:跳頁
    PageJump() {
      if (this.PageInfo.PageJump > this.PageInfo.PageTotalP) {
        alert('超出最後一頁')
        return false
      }
      this.PageInfo.PageIndex = this.PageInfo.PageJump
      this.ListData()
    },
    ListData() {
      //Search 篩選數據
      let filterData = JSON.parse(JSON.stringify(this.RoomsRoomList))
      if (this.Modal['RoomType']) {
        filterData = filterData.filter(
          (item) => item.type == this.Modal['RoomType'],
        )
      }
      if (this.Modal['RoomNumbers'].length > 0) {
        filterData = filterData.filter((item) => {
          return this.Modal['RoomNumbers'].every((num) =>
            item.numberList.includes(num),
          )
        })
      }
      //設定頁面參數
      this.PageInfo['PageTotalP'] = Math.ceil(
        filterData.length / this.PageInfo['PageAmount'],
      )
      this.PageInfo['Pageli'] = []
      for (let index = 1; index <= this.PageInfo['PageTotalP']; index++) {
        this.PageInfo['Pageli'].push(index)
      }
      //依照頁面參數篩選
      let startIndex =
        (this.PageInfo['PageIndex'] - 1) * this.PageInfo['PageAmount']
      let endIndex = this.PageInfo['PageIndex'] * this.PageInfo['PageAmount']
      this.PageInfo['Result'] = filterData.slice(startIndex, endIndex)
    },
    ResetModal() {
      this.Modal['RoomId'] = ''
      this.Modal['RoomType'] = ''
      this.Modal['RoomNumbers'] = []
      this.Modal['RoomTypeOptions'] = []
    },
    // 新增、修改、搜尋:關閉
    AllClose() {
      this.OpenKey = ''
      this.ResetModal()
      this.CancelSearchingRoomNumber()
    },
    // 搜尋:開啟
    SearchOpen() {
      //初始化
      this.OpenKey = 'Search'
      this.ResetModal()
    },
    // 搜尋:查詢
    SearchSubmit() {
      this.ListData()
      this.OpenKey = ''
      this.CancelSearchingRoomNumber()
    },
    // 新增:開啟
    AddOpen() {
      if (this.Auth.MyBraUsersPower.BraPA != 1) {
        alert('沒有新增權限')
        return false
      }
      //初始化
      this.OpenKey = 'Add'
      this.ResetModal()
    },
    // 新增:送出
    AddSubmit() {
      if (this.Auth.MyBraUsersPower.BraPA != 1) {
        alert('沒有新增權限')
        return false
      }
      let mainOption = this.Modal['RoomTypeOptions'][0] || ''
      let otherOptions = this.Modal['RoomTypeOptions'].filter(
        (item, i) => i > 0,
      )
      let obj = {
        roomId: new Date().getTime() + Math.floor(Math.random() * 1000),
        type: this.Modal['RoomType'],
        number: this.Modal['RoomNumbers'].join(','),
        mainOption: mainOption?.name,
        otherOptions: otherOptions.map((item) => item?.name),
        isModified: true, //如果有被修改，則變成true，底色就會變成黃色
        numberList: [...this.Modal['RoomNumbers']],
        optionList: [...this.Modal['RoomTypeOptions']],
      }
      this.RoomsRoomList.unshift(obj)
      this.AllClose()
      this.ListData()
    },
    // 編輯:開啟
    ModifyOpen(index) {
      if (this.Auth.MyBraUsersPower.BraPM != 1) {
        alert('沒有編輯權限')
        return false
      }
      //初始化
      this.OpenKey = 'Modify'
      this.ResetModal()
      let item = this.PageInfo['Result'][index]
      this.Modal['RoomId'] = index
      this.Modal['RoomType'] = item.type
      this.Modal['RoomNumbers'] = [...item.numberList]
      this.Modal['RoomTypeOptions'] = [...item.optionList]
    },
    // 編輯:送出
    ModifySubmit() {
      if (this.Auth.MyBraUsersPower.BraPM != 1) {
        alert('沒有編輯權限')
        return false
      }
      let index = Number(this.Modal.RoomId)
      let item = JSON.parse(JSON.stringify(this.PageInfo['Result'][index]))

      item.type = this.Modal['RoomType']
      item.numberList = [...this.Modal['RoomNumbers']]
      item.number = this.Modal['RoomNumbers'].join(',')
      item.isModified = true
      item.optionList = [...this.Modal['RoomTypeOptions']]
      let mainOption = this.Modal['RoomTypeOptions'][0] || ''
      let otherOptions = this.Modal['RoomTypeOptions'].filter(
        (item, i) => i > 0,
      )
      item.mainOption = mainOption?.name
      item.otherOptions = otherOptions.map((item) => item?.name)

      let roomId = item.roomId
      let theIndex = this.RoomsRoomList.findIndex(
        (obj) => obj?.roomId == roomId,
      )
      this.RoomsRoomList.splice(theIndex, 1)
      this.RoomsRoomList.unshift(item)
      this.AllClose()
      this.ListData()
    },
    // 刪除:開啟
    DeleteOpen(index) {
      if (this.Auth.MyBraUsersPower.BraPD != 1) {
        alert('沒有刪除權限')
        return false
      }
      //初始化
      this.ResetModal()
      let item = this.RoomsRoomList[index]
      this.Modal['RoomId'] = index
      this.Modal['RoomNumbers'] = [...item['numberList']]
    },
    // 刪除:送出
    DeleteSubmit() {
      if (this.Auth.MyBraUsersPower.BraPD != 1) {
        alert('沒有刪除權限')
        return false
      }
      let index = Number(this.Modal['RoomId'])
      this.RoomsRoomList.splice(index, 1)
      this.ResetModal()
      this.ListData()
    },
    // 按下表單
    ClickList(index) {
      let ResultData = this.PageInfo.Result[index]
      ResultData.isModified = false
      let roomId = ResultData.roomId
      let result = this.RoomsRoomList.filter((item) => item?.roomId == roomId)
      if (result[0]) {
        result[0].isModified = false
      }
    },
    // 表單背景顏色
    getResultStyle(bool){
      let obj = {
        backgroundColor: bool ? '#f1e9b3' : '',
        color: bool ? '#3d3d3d' : ''
      }
      return obj
    },
    //選擇房型:選擇li
    SelectRoomType(Item) {
      if (this.Modal.RoomType == Item) {
        return (this.Modal.RoomType = '') //如果按下的是原房型，則取消選擇
      }
      this.Modal.RoomType = Item
    },
    //選擇房型:取消
    CancelRoomType() {
      this.Modal.RoomType = ''
    },
    //選擇房號:選擇li
    SelectRoomNumbers(Item) {
      let number = Item?.number
      let SelectedRoomNumberIndex = this.Modal.RoomNumbers.findIndex(
        (str) => str == number,
      )
      //判斷按下的是否是原房號
      if (SelectedRoomNumberIndex < 0) {
        //不是，就push
        this.Modal.RoomNumbers.push(number)
      } else {
        //是，就移除
        this.Modal.RoomNumbers.splice(SelectedRoomNumberIndex, 1)
      }
    },
    //選擇房號:li選單背景顏色的判別
    FindedRoomNumber(Number) {
      //被選擇的 room number li ，true 背景會變色， false 背景是白色
      if (!Number) return false
      let SelectedRoomNumber = this.Modal.RoomNumbers.find(
        (str) => str == Number,
      )
      if (!SelectedRoomNumber) return false
      return true
    },
    // 選擇房號:[搜尋房號]
    SearchRoomNumber() {
      if(this.SearchingRoomNumber){
        this.FilteredRoomsNumberList['regular'] = this.RoomsNumberList['regular'].filter(item=>item.number == this.SearchingRoomNumber)
        this.FilteredRoomsNumberList['double'] = this.RoomsNumberList['double'].filter(item=>item.number == this.SearchingRoomNumber)
        this.FilteredRoomsNumberList['fancy'] = this.RoomsNumberList['fancy'].filter(item=>item.number == this.SearchingRoomNumber)
      }else{
        this.FilteredRoomsNumberList['regular'] = JSON.parse(JSON.stringify(this.RoomsNumberList['regular']))
        this.FilteredRoomsNumberList['double'] = JSON.parse(JSON.stringify(this.RoomsNumberList['double']))
        this.FilteredRoomsNumberList['fancy'] = JSON.parse(JSON.stringify(this.RoomsNumberList['fancy']))
      }
    },
    //選擇房號:取消[搜尋房號]
    CancelSearchingRoomNumber() {
      this.SearchingRoomNumber = ''
      this.SearchRoomNumber()
    },
    //選擇房號:取消
    CancelRoomNumbers() {
      this.Modal.RoomNumbers = []
    },
    //選擇房號:確定
    ChangeRoomModal2ConfirmBtn() {
      //搜尋時，選擇完房型跟房號，按下這個按鈕時，就送出request
      if (this.OpenKey == 'Search') {
        this.SearchSubmit()
      }
    },
    //可支援房型列表
    isSelected(option) {
      return this.Modal.RoomTypeOptions.find(
        (selected) => selected.code === option.code,
      )
    }
  },
  // 物件搭建好後執行
  mounted() {
    //假資料 (開啟權限)
    ;(function (self) {
      self.Auth.MyBraUsersPower.BraPV = 1
      self.Auth.MyBraUsersPower.BraPA = 1
      self.Auth.MyBraUsersPower.BraPM = 1
      self.Auth.MyBraUsersPower.BraPD = 1
      self.Auth.MyBraUsersPower.BraPG = 1
      self.Auth.MyBraUsersPower.BraP1 = 1
      self.Auth.MyBraUsersPower.BraP2 = 1
      self.Auth.MyBraUsersPower.BraP3 = 1
    })(this)

    // 檢視權限
    if (this.Auth.MyBraUsersPower.BraPV != 1) {
      alert('沒有:檢視權限')
      this.$router.push({ path: '/', replace: true })
    }

    // 取得列表資料
    this.ListData()
    this.SearchRoomNumber()
  },
}
</script>
